.container {}

.ratingContainer {
}
.rating {
  transition: width .5s;
}
.ratingSelect {
  color: var(--color-select);
}

.avatarContainer {
  display: flex;
  overflow: hidden;
  justify-content: center;
  align-items: center;
  width: 46px;
  padding: 2px;
  border: solid 2px white;
  border-radius: 40px;
}
.avatarSelect {
  border: solid 2px var(--color-select);
}

.details {}
.info {
  display: flex;
  align-items: baseline;
  gap: 8px;
  color: var(--color-info);
  line-height: 16px;
}
.nameContainer {
  white-space: nowrap;
}
.name {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.infoPartition {
  width: 2px;
  height: 12px;
  background: var(--color-info);
}

.penalty {
  color: var(--color-penalty);
}
.penaltySelect {
  color: var(--color-penalty-select);
}